import { parse } from "path";

//把时间戳格式化成年月日
 export function formatDate(timestamp,type) {
     let date = new Date(timestamp);
     let year = date.getFullYear();
     let month = addZero(date.getMonth() + 1);
     let day = addZero(date.getDate());
     let hours = addZero(date.getHours());
     let minutes = addZero(date.getMinutes());
     let seconds = addZero(date.getSeconds());
     if(type==1){
     return year + '-' + month + '-' + day + ' ' + hours + ':' + minutes + ':' + seconds;

     }else{
        return year + "-" + month + "-" + day;

     }
 }

 export  function addZero(num) {
    return num < 10 ? "0" + num : num;
}

export  function parseToNumber(str){
    return parseInt(str);
}

//把2019-01-03T00:18:21.000+0000转化成正常格式
export function renderTime(date) {
    let dateee = new Date(date).toJSON();
    return new Date(+new Date(dateee) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '') 
  }


//gps坐标转百度坐标

/**
 * 单个坐标转换
 * @param point
 * @returns {BMap.Point}
 */
export function Gps2Bd(point){
    let _t = wgs2bd(point.lat,point.lng);
    let _BPoint = new BMap.Point(_t[1], _t[0]);
    return _BPoint;
}
 
/**
 * 批量坐标转换
 * @param points
 * @returns {Array}
 */
export  function Gps2Bds(points){
    let resultPoints = [];
    points.forEach(function(point) {
        let _t = wgs2bd(point.lat,point.lng);
        let _BPoint = new BMap.Point(_t[1], _t[0]);
        resultPoints.push(_BPoint);
    });
    return resultPoints;
}
 
//////////////////////////////////////////
//////////////转换核心代码////////////////
//////////////////////////////////////////
let pi = 3.14159265358979324;
let a = 6378245.0;
let ee = 0.00669342162296594323;
let x_pi = 3.14159265358979324*3000.0/180.0;
 
 
//世界大地坐标转为百度坐标
function wgs2bd(lat,lon) {
    let wgs2gcjR = wgs2gcj(lat, lon);
    let gcj2bdR = gcj2bd(wgs2gcjR[0], wgs2gcjR[1]);
    return gcj2bdR;
}
 
function gcj2bd(lat,lon) {
    let x = lon, y = lat;
    let z = Math.sqrt(x * x + y * y) + 0.00002 * Math.sin(y * x_pi);
    let theta = Math.atan2(y, x) + 0.000003 * Math.cos(x * x_pi);
    let bd_lon = z * Math.cos(theta) + 0.0065;
    let bd_lat = z * Math.sin(theta) + 0.006;
    let result = [];
    result.push(bd_lat);
    result.push(bd_lon);
    return result;
}
 
function bd2gcj(lat,lon) {
    let x = lon - 0.0065, y = lat - 0.006;
    let z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * x_pi);
    let theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * x_pi);
    let gg_lon = z * Math.cos(theta);
    let gg_lat = z * Math.sin(theta);
    let result = [];
    result.push(gg_lat);
    result.push(gg_lon);
    return result;
}
 
function wgs2gcj(lat,lon) {
    let dLat = transformLat(lon - 105.0, lat - 35.0);
    let dLon = transformLon(lon - 105.0, lat - 35.0);
    let radLat = lat / 180.0 * pi;
    let magic = Math.sin(radLat);
    magic = 1 - ee * magic * magic;
    let sqrtMagic = Math.sqrt(magic);
    dLat = (dLat * 180.0) / ((a * (1 - ee)) / (magic * sqrtMagic) * pi);
    dLon = (dLon * 180.0) / (a / sqrtMagic * Math.cos(radLat) * pi);
    let mgLat = lat + dLat;
    let mgLon = lon + dLon;
    let result = [];
    result.push(mgLat);
    result.push(mgLon);
    return result;
}
 
function transformLat(lat,lon) {
    let ret = -100.0 + 2.0 * lat + 3.0 * lon + 0.2 * lon * lon + 0.1 * lat * lon + 0.2 * Math.sqrt(Math.abs(lat));
    ret += (20.0 * Math.sin(6.0 * lat * pi) + 20.0 * Math.sin(2.0 * lat * pi)) * 2.0 / 3.0;
    ret += (20.0 * Math.sin(lon * pi) + 40.0 * Math.sin(lon / 3.0 * pi)) * 2.0 / 3.0;
    ret += (160.0 * Math.sin(lon / 12.0 * pi) + 320 * Math.sin(lon * pi  / 30.0)) * 2.0 / 3.0;
    return ret;
}
 
function transformLon(lat,lon) {
    let ret = 300.0 + lat + 2.0 * lon + 0.1 * lat * lat + 0.1 * lat * lon + 0.1 * Math.sqrt(Math.abs(lat));
    ret += (20.0 * Math.sin(6.0 * lat * pi) + 20.0 * Math.sin(2.0 * lat * pi)) * 2.0 / 3.0;
    ret += (20.0 * Math.sin(lat * pi) + 40.0 * Math.sin(lat / 3.0 * pi)) * 2.0 / 3.0;
    ret += (150.0 * Math.sin(lat / 12.0 * pi) + 300.0 * Math.sin(lat / 30.0 * pi)) * 2.0 / 3.0;
    return ret;
}


 