import request from '../../request'
//新增银行
export function addBank(data) {
    return request({
        url: '/eoms/bankGroups/add',
        method: 'post',
        data
    })
}
//删除银行
export function removeBank(data) {
    return request({
        url: '/eoms/bankGroups/remove',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
//查询银行
export function selectBank(data) {
    return request({
        url: '/eoms/bankGroups/select',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
//银行列表
export function bankList(data) {
    return request({
        url: '/eoms/bankGroups/page',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
//设置银行所属库包rfid头(编辑)
export function setRfidHead(data) {
    return request({
        url: '/eoms/bankGroups/setRfidHead',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}


