<template>
  <router-view v-if="$route.path!=='/networkManagement'"></router-view>

  <div class="app-container" id="networkManagement" v-else>
    <div class="filter-container">
      <el-input v-model="keyword" placeholder="请输入关键字搜索" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-select v-model="networkVal" class="filter-item" placeholder="请选择网点类型" @change="handleFilter">
        <el-option v-for="item in networkList" :key="item.type" :label="item.option" :value="item.type" />
      </el-select>
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="handleFilter">搜索</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru">导入</el-button>

    </div>
    <div class="tableBox">

      <el-table v-loading="listLoading" :data="list" fit highlight-current-row :row-class-name="tableRowClassName">
        <el-table-column label="序号" width="60" align="center" prop="id" type="index" :index="indexMethod"></el-table-column>
        <!-- <el-table-column v-if="power==1" label="ID" align="center" prop="id"></el-table-column> -->
        <el-table-column label="网点名称" min-width="150" align="center" prop="name"></el-table-column>
        <el-table-column label="所属银行" min-width="150" align="center" prop="parentBankName"></el-table-column>
        <el-table-column label="网点地址" min-width="300" align="center" prop="address"></el-table-column>
        <el-table-column label="早送任务预达时间" width="130" align="center" prop="sendTime"></el-table-column>
        <el-table-column label="晚收任务预达时间" width="130" align="center" prop="receiveTime"></el-table-column>
        <el-table-column label="网点类型" width="120" align="center" prop="networkType"></el-table-column>
        <el-table-column label="是否报站" width="80" align="center" prop="networkType">
          <template slot-scope="{row}">
            {{row.needToReport==2?'报站':'不报站'}}
          </template>
        </el-table-column>
        <el-table-column label="网点响应半径(米)" width="150" align="center" prop="radius"></el-table-column>
        <el-table-column label="建立时间" width="200" align="center" prop="createTime"></el-table-column>

        <el-table-column fixed="right" label="操作" align="center" min-width="230" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" icon="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination v-if="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
    <my-upload :visible.sync="uploadVisible" :uploadData="{type:7}" @dataUpdated="reqSuccess"></my-upload>

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination
// 请求的js文件
import { lstPt, dellstPt } from '../../../api/managementArchives/network'
import { formatDate } from "../../../../src/common";// 转换时间格式
import {
  exportExcelReq
} from "../../../api/managementArchives/common"; //执行统计（在数据没有被查询过的情况下可能查询不到，需要执行统计一段时间后再查询就会有结果了）,导出

export default {
  name: "ComplexTable",
  components: { Pagination, myUpload },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem('userInfo')).Power,// 权限
      list: [],// 表格列表
      total: 0,// 表格总数
      listLoading: false,// 表格loading
      listQuery: {
        page: 1,
        limit: 10
      },
      indexMethod: 1,// 序号
      networkList: [// 网点类型
        { type: -1, option: '全部网点' },
        { type: 1, option: '营业厅' },
        { type: 2, option: 'ATM' },
        { type: 3, option: '营业网点安全区' },
        { type: 4, option: '收款点' },
        { type: 5, option: '路口' },
        { type: 6, option: '终点收工点' },
        { type: 7, option: '车辆打卡区域' },
      ],
      networkVal: -1,// 默认的网点类型
      exportLoading: false,
      uploadVisible: false,
      keyword:''
    };
  },
  watch: {
    //没有子路由不会触发
    // $route(val) {
    //   let D = false;

    //   if (val.params.page) {// 存在页码
    //     D = val.params;
    //   };
    //   this.getData(D);

    // }
  },
  created() {
    if(this.$route.params.page&&this.$route.params.limit){
    this.listQuery.page=this.$route.params.page;
    this.listQuery.limit=this.$route.params.limit;
    }
    this.getList()
  },

  methods: {
     deleteConfirmOpen(row) {
      this.$confirm('确定进行此操作吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteRoute(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    reqSuccess: function (val) {  //val用于接收子组件传过来的值
      if (val == true) {
        this.getList();
      }
    },
    getList() {// 生成列表
      let _t = this;
      _t.listLoading = true;
      lstPt({
        type: _t.networkVal,
        page: _t.listQuery.page,
        limit: _t.listQuery.limit,
        keyword:_t.keyword
      })
        .then(res => {
          if (res.status == 0) {
            _t.list = res.data.records;
            _t.list.forEach((item, index) => {
              _t.list[index].createTime = formatDate(item.createTime, 1);
              _t.list[index].networkType = _t.networkList[item.contentType].option;
            });
            _t.total = res.data.total;
            _t.indexMethod = (_t.listQuery.page - 1) * (_t.listQuery.limit) + 1;
          } else {
            _t.$message.error(res.msg);
          };
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    handleFilter(e) {// 查询
      // this.networkVal = e;
      this.listQuery.page = 1;
      this.getList();
    },
    // 页面间传输的数据
    getData(D) {
      let _t = this;
      if (D) {
        _t.total = 0;
        _t.listQuery.page = D.page;
        _t.listQuery.limit = D.limit;
      };
      _t.getList();
    },
    handleCreate() {// 添加网点
      // this.$router.push("/networkAdd");
      this.$router.push({ name: '网点添加或编辑', params: { page: this.listQuery.page, limit: this.listQuery.limit } });
    },
    handleUpdate(row) {// 编辑网点
      // console.log(row)
      // 把页码分页发到地图页中
      row.page = this.listQuery.page;
      row.limit = this.listQuery.limit;
      this.$router.push({ name: '网点添加或编辑', params: row });
    },
    deleteRoute(row) {// 刪除网点
      let _t = this;
      _t.listLoading = true;
      dellstPt({ id: row.id }).then(res => {
        if (res.status == 0) {
          _t.$message.success('刪除成功');
          if (_t.listQuery.page > 1 && _t.list.length <= 1) {// 最后一条返回上一页
            _t.listQuery.page -= 1;
          };
          _t.getList();
        } else {
          _t.$message.error(res.msg);
          _t.listLoading = false;
        };
      }).catch(err => {
        _t.listLoading = false;
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      this.exportLoading = true;
      // 查找数据
      exportExcelReq({ excelType: 9, title: '网点信息' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "网点信息" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        this.exportLoading = false;
      });
    }
  }
};
</script>