<template>
  <div class="app-container" id="bank">
    <div class="filter-container">
      <el-button class="filter-item" type="primary" @click="handleCreate" v-if="power==1" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru">导入</el-button>


    </div>
    <div class="tableBox">

      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" fixed sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="名称" min-width="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.name }}</span>
          </template>
        </el-table-column>
        <el-table-column label="银行所属库包标签号头" min-width="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.startWith }}</span>
          </template>
        </el-table-column>
        <el-table-column label="更新时间" min-width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ formatDateTime(scope.row.updateTime) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ formatStatus(scope.row.status )}}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作" fixed="right" align="center" min-width="230" class-name="small-padding fixed-width" v-if="power==1">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" :disabled="row.status==2" icon="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
    <el-dialog id="myDialog" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="名称" prop="name" v-if="dialogStatus=='create'">
          <el-input v-model="temp.name " />
        </el-form-item>
        <el-form-item label="标签号头部" prop="startWith">
          <el-input v-model="temp.startWith " />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
<my-upload :visible.sync="uploadVisible"  :uploadData="{type:6}" @dataUpdated="reqSuccess"></my-upload>

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination
import {addBank, removeBank, bankList, setRfidHead} from "../../../api/managementArchives/bank";
import { formatDate } from "../../../../src/common"; //时间格式化
import {exportExcelReq} from "../../../api/managementArchives/common";

let myLoading = undefined; // 弹窗服务调用

export default {
  name: "bank",
  components: { Pagination,myUpload },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null,
      total: 0, 
      indexMethod: 1, // 序号
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 10,
        name: undefined
      },
      teamLeaderOptions: [{ id: -1, name: "请选择" }], //队长下拉数据
      geometryOptions: [{ id: -1, name: "请选择" }], //区域下拉数据
      teamStatusOptions: [
        { key: 0, display_name: "正常" },
        { key: 1, display_name: "取消" }
      ], 
     
      temp: {
      },
      dialogFormVisible: false, 
      dialogStatus: "", 
      textMap: {
        update: "编辑",
        create: "添加"
      },
      //添加或编辑表单校验
      rules: {
        name: [
          { required: true, message: "请填写银行名称", trigger: "blur" }
        ],
        startWith: [
          { required: true, message: "请填写银行所属库包标签号头部", trigger: "blur" }
        ],

        status: [
          { required: true, message: "请选择大队状态", trigger: "change" }
        ]
      },
      exportLoading: false,
      uploadVisible:false,
     
    };
  },
  created() {
    this.getList(); //初始化获取列表
  },
  methods: { 
     deleteConfirmOpen(row) {
      this.$confirm('确定进行此操作吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.handleDelete(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    reqSuccess: function(val){  //val用于接收子组件传过来的值
      if(val==true){
        this.getList();
      }
    },
    getList() {
      let that = this;
      this.listLoading = true;
      bankList({
        page: this.listQuery.page,
        limit: this.listQuery.limit,
        status:[1]
      })
        .then(res => {
          if (res.status == 0) {
            that.listLoading = false;
            that.list = res.data;
            that.total = res.count;
            that.indexMethod =
              (that.listQuery.page - 1) * that.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          that.listLoading = false;
        });
    },
  
    //删除
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      removeBank({
        id: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("刪除成功");
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },

    //重置
    resetTemp() {
      this.temp = {
        name: "",
        status: 0,
        createTimeShow: null,
        startWith: ''
      };
    },
 
 
    //点击增加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //增加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          if (this.temp.createTimeShow) { this.temp.createTime = new Date(this.temp.createTimeShow).getTime(); }
          let subData = { ...this.temp };
          delete subData.createTimeShow
          addBank({ name: this.temp.name, rfidHead: this.temp.startWith })
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("添加成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = {
        id: row.id,
        status: row.status,
        createTimeShow: row.createTimeShow,
        startWith: row.startWith
      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          if (this.temp.createTimeShow) {

            this.temp.createTime = new Date(this.temp.createTimeShow).getTime();
          }
          let subData = { id: this.temp.id, rfidHead: this.temp.startWith };
          setRfidHead(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("编辑成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
  

    //格式化时间
    formatDateTime(timestamp) {
      return formatDate(timestamp, 1);
    },
   
    formatStatus(status) {
      if (status == 1) {
        return "存在";
      } else if (status == 2) {
        return "取消";
      }
    },
    
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
      exportExcel() {
        let _t = this;
      this.exportLoading = true;
        // 查找数据
        exportExcelReq({ excelType: 27, title: '银行信息表' }).then(res => {
           try {
              let enc = new TextDecoder('utf-8')
              let data = JSON.parse(enc.decode(new Uint8Array(res)))
              this.$message({
                message: data.msg,
                type: 'warning'
              });

            } catch (error) {
          let fileName = "银行信息表" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
            }
        }).finally(() => {
        this.exportLoading = false;
      });
      }

  }
};
</script>
<style lang="scss">
#bank {
  .el-button--danger.is-disabled {
    color: #fff;
    background-color: #fab6b6;
    border-color: #fab6b6;
  }
}
</style>









