<template>
  <div id="networkAdd">
    <div class="left">
      <el-form v-loading="myLoading" class="routeForm" ref="temp" :model="temp" :rules="rules" label-position="left" style="margin-left:0;">
        <el-form-item prop="networkVal">
          <el-select v-model="temp.networkVal" @change="changeType" class="filter-item" placeholder="请选择网点类型">
            <el-option v-for="item in networkList" :key="item.type" :label="item.option" :value="item.type" />
          </el-select>
        </el-form-item>
        <el-form-item prop="bank">
          <el-select filterable v-model="temp.bank" class="filter-item" placeholder="请选择银行">
            <el-option v-for="item in bankOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.status==2" />
          </el-select>
        </el-form-item>
        <el-form-item prop="roadName">
          <el-input placeholder="请输入网点名称" v-model="temp.roadName" />
        </el-form-item>
        <el-form-item prop="sendTime">
          <el-time-picker v-model="temp.sendTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder="早送任务预达时间"></el-time-picker>
        </el-form-item>
        <el-form-item prop="receiveTime">
          <el-time-picker v-model="temp.receiveTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder="晚收任务预达时间"></el-time-picker>

        </el-form-item>
        <el-form-item prop="networkSearchVal">
          <el-input id="suggestId" v-model="temp.networkSearchVal" placeholder="请输入网点地址" />
          <div id="r-result"></div>
        </el-form-item>

        <el-form-item v-if="!clockIn" prop="finalMarkePoints" class="lnglat">
          <el-input placeholder="请输入经纬度" v-model="temp.finalMarkePoints" />
        </el-form-item>

        <el-form-item v-if="clockIn" prop="pts" class="lnglat">
          <el-input placeholder="请在地图上绘制区域" v-model="temp.pts" />
        </el-form-item>
        <el-form-item prop="needToReport">
          <el-select v-model="temp.needToReport" class="filter-item" placeholder="请选择是否报站">
            <el-option v-for="item in needToReportOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item v-if="!clockIn" prop="radius" :label="`网点半径 ${myRadius>1000?`${myRadius/1000} 公里`:`${myRadius} 米`}`" class="radius">
          <el-slider v-model="temp.radius" show-input input-size="mini" :max="radiusMax" :format-tooltip="formatTooltip" @input="changeRadius"></el-slider>
        </el-form-item>

        <el-form-item>
          <el-button v-show="!clockIn" type="primary" class="addNetwork" @click="confirmAddNetwork('temp')">提交</el-button>
          <el-button v-show="clockIn" type="primary" class="addNetwork" @click="clockSubmit('temp')">提交</el-button>
          <el-button type="primary" class="addNetwork" @click="goBack">返回</el-button>
        </el-form-item>

        <p class="tip">如网点地址未查询到或者偏移，请手动在地图上进行处理</p>
      </el-form>
    </div>
    <div class="content">
      <div id="historyRouteMap" ref="historyRouteMap"></div>
    </div>
    <div class="opr">
      <el-button v-show="!clockIn" type="primary" size="small" round class="oprbtn" @click="setPoint">设置网点</el-button>
      <el-button v-show="clockIn" type="primary" size="small" round class="oprbtn" @click="setArea">设置区域</el-button>
      <el-button type="primary" size="small" round class="oprbtn" @click="clearAll">清除</el-button>
    </div>
  </div>
</template>
<script>
// 请求的js文件
import {
  addlstPt,
  editlstPt,
  checkArea
} from "../../../api/managementArchives/network";
import { selectBank } from '../../../api/managementArchives/bank';

export default {
  data() {
    var addressV = (rule, value, callback) => {
      // 验证经纬度是否添加
      // 搜索地址不为空时，验证有无经纬度
      if (this.temp.networkSearchVal != "") {
        if (value == "") {
          callback(new Error("该地址未标记，请手动进行标记"));
        } else {
          callback();
        }
      } else {
        callback();
      }
    };
    var areaV = (rule, value, callback) => {
      // 验证区域是否添加
      // 搜索地址不为空时，验证有无经纬度
      if (this.temp.networkSearchVal != "") {
        if (value == "") {
          callback(new Error("请在地图上绘制区域"));
        } else {
          callback();
        }
      } else {
        callback();
      }
    };
    return {
      id: undefined,
      map: undefined,
      myCircle: undefined, // 圆
      radiusMax: 10000,
      prevData: {}, // 上一页传过来的数据
      myLoading: false, // 表单loading
      searchMarke: [],
      clockIn: false, // 是否为车辆打卡
      myPolygon: undefined, // 多边形
      myM: undefined, // 点
      myDrawingManager: undefined, // 鼠标工具
      myDobj: {
        isOpen: false, //是否开启绘制模式
        enableDrawingTool: false, //是否显示工具栏
        drawingToolOptions: {
          anchor: BMAP_ANCHOR_TOP_RIGHT, //位置
          offset: new BMap.Size(5, 5), //偏离值
          drawingModes: [BMAP_DRAWING_MARKER, BMAP_DRAWING_POLYGON]
        },
        polygonOptions: {
          strokeColor: "red", //边线颜色。
          fillColor: "#e28080", //填充颜色。当参数为空时，圆形将没有填充效果。
          strokeWeight: 3, //边线的宽度，以像素为单位。
          strokeOpacity: 0.8, //边线透明度，取值范围0 - 1。
          fillOpacity: 0.6, //填充的透明度，取值范围0 - 1。
          strokeStyle: "solid" //边线的样式，solid或dashed。
        }
      }, //  鼠标工具对象·
      networkList: [
        // 网点类型
        { type: 1, option: "营业厅" },
        { type: 2, option: "ATM" },
        // { type: 3, option: "营业网点安全区" },
        { type: 4, option: "收款点" },
        // { type: 5, option: "路口" },
        { type: 6, option: "终点收工点" },
        // { type: 7, option: "车辆打卡区域" }
      ],
      networkAddLocal: {},
      userlocation: {}, // 查询出来的经纬度对象
      temp: {
        roadName: "", // 网点名称
        networkSearchVal: "", // 搜索地址
        networkVal: "", // 默认的网点类型
        finalMarkePoints: "", // 经纬度
        radius: 50, // 网点半径
        pts: "", // 地图区域
        bank: "",
        sendTime: "",
        receiveTime: "",
        needToReport: 2,

      },
      rules: {
        roadName: [
          { required: true, message: "请输入网点名称", trigger: "blur" }
        ],
        networkSearchVal: [
          { required: true, message: "请输入网点地址", trigger: "blur" }
        ],
        finalMarkePoints: [{ validator: addressV, trigger: "blur" }],
        networkVal: [
          { required: true, message: "请选择网点类型", trigger: "change" }
        ],
        bank: [
          { required: true, message: "请选择银行", trigger: "change" }
        ],
        pts: [{ validator: areaV, trigger: "blur" }],
        needToReport: [
          { required: true, message: "请选择是否报站", trigger: "change" }
        ],
      },
      bankOptions: [],
      needToReportOptions: [
        { id: 1, name: '不报站' },
        { id: 2, name: '报站' }

      ],
    };
  },
  computed: {
    myRadius: {
      get() {
        return this.temp.radius * 1;
      },
      set(val) {
        this.temp.radius = val / 1;
      }
    }
  },
  mounted() {
    this.initMap();
  },
  methods: {
    //创建和初始化地图函数：
    initMap() {
      this.createMap(); //创建地图
      this.setMapEvent(); //设置地图事件
      this.addMapControl(); //向地图添加控件
      this.getSelect();
    },
    // 创建地图函数：
    createMap() {
      let _t = this;
      _t.map = new BMap.Map("historyRouteMap", { enableMapClick: false }); //在百度地图容器中创建一个地图
      // var point = new BMap.Point(113.925973657, 22.5760535462); //定义一个中心点坐标
      // _t.map.centerAndZoom(point,13); //设定地图的中心点和坐标并将地图显示在地图容器中
      _t.map.enableScrollWheelZoom();
      _t.getPrevData(); // 获取上一页数据
      if (_t.id == undefined) {
        _t.map.centerAndZoom("潮州", 13);
        // var myCity = new BMap.LocalCity();
        // myCity.get(function(res){
        //   var cityName = res.name;
        //   _t.map.setCenter(cityName);
        // });
      }

      // 点击地图添加标注
      // _t.map.addEventListener('click',function(e){
      //   _t.drawMarker(e.point);// 添加标注
      // });

      // 网点地址查询
      var ac = new BMap.Autocomplete({
        // 百度api关键字查询
        //建立一个自动完成的对象
        input: "suggestId",
        location: _t.map,
        onSearchComplete: function (e) {
          // 输入时清空地图上的标志，清除经纬度
          _t.clearAll();
        }
      });
      ac.setInputValue(_t.temp.networkSearchVal); // 必须设置默认值。否则会被清空
      var myValue;
      ac.addEventListener("onconfirm", function (e) {
        // console.log(e);
        //鼠标点击下拉列表后的事件
        var _value = e.item.value;
        myValue =
          _value.province +
          _value.city +
          _value.district +
          _value.street +
          _value.business;
        _t.temp.networkSearchVal = myValue;
        _t.setPlace();
      });
    },

    //地图事件设置函数：
    setMapEvent() {
      this.map.enableDragging(); //启用地图拖拽事件，默认启用(可不写)
      this.map.enableScrollWheelZoom(); //启用地图滚轮放大缩小
      this.map.enableDoubleClickZoom(); //启用鼠标双击放大，默认启用(可不写)
      this.map.enableKeyboard(); //启用键盘上下左右键移动地图
    },

    //地图控件添加函数：
    addMapControl() {
      //向地图中添加缩放控件
      var ctrl_nav = new BMap.NavigationControl({
        anchor: BMAP_ANCHOR_TOP_LEFT,
        type: BMAP_NAVIGATION_CONTROL_LARGE
      });
      // this.map.addControl(ctrl_nav);
      //向地图中添加比例尺控件
      var ctrl_sca = new BMap.ScaleControl({ anchor: BMAP_ANCHOR_BOTTOM_LEFT });
      var overView = new BMap.OverviewMapControl();

      var mapType1 = new BMap.MapTypeControl({
        mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP],
        anchor: BMAP_ANCHOR_TOP_LEFT
      });
      // this.map.addControl(ctrl_sca);

      this.map.addControl(mapType1); //2D图，混合图
    },

    //移除地图类型和缩略图
    delete_control() {
      this.map.removeControl(mapType1); //移除2D图，混合图
      this.map.removeControl(overView);
      this.map.removeControl(overViewOpen);
    },
    // 提交
    confirmAddNetwork(temp) {
      let _t = this;
      // 如果id存在则为编辑
      this.$refs[temp].validate((valid, obj) => {
        if (valid) {
          _t.myLoading = true;

          if (_t.id) {
            // 编辑
            let myData = {
              id: _t.id,
              name: _t.temp.roadName,
              address: _t.temp.networkSearchVal,
              contentType: _t.temp.networkVal,
              geom: `POINT(${_t.temp.finalMarkePoints.split(",")[0]} ${_t.temp.finalMarkePoints.split(",")[1]
                })`,
              radius: _t.myRadius,
              geomtype: _t.prevData.geomtype,

              status: _t.temp.status,
              needToReport: _t.temp.needToReport
            };
            if (_t.temp.sendTime) {
              myData.sendTime = _t.temp.sendTime;
            }
            if (_t.temp.receiveTime) {
              myData.receiveTime = _t.temp.receiveTime;
            }
            // if (_t.temp.networkVal == 1 || _t.temp.networkVal == 2) {
            let obj = {};
            obj = this.bankOptions.find((item) => {
              return item.id === _t.temp.bank;
            });
            let bankName = ''
            bankName = obj.name;
            myData.parentBankId = _t.temp.bank;
            myData.parentBankName = bankName
            // }
            editlstPt(myData)
              .then(res => {
                if (res.success) {
                  _t.$message.success("编辑成功");
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.myLoading = false;
              });
          } else {

            // 添加
            let myData = {
              name: _t.temp.roadName,
              address: _t.temp.networkSearchVal,
              type: _t.temp.networkVal,
              lng: _t.temp.finalMarkePoints.split(",")[0],
              lat: _t.temp.finalMarkePoints.split(",")[1],
              radius: _t.myRadius,
              needToReport: _t.temp.needToReport


            };
            if (_t.temp.sendTime) {
              myData.sendTime = _t.temp.sendTime;
            }
            if (_t.temp.receiveTime) {
              myData.receiveTime = _t.temp.receiveTime;
            }

            // if (_t.temp.networkVal == 1 || _t.temp.networkVal == 2) {
            let obj = {};
            obj = this.bankOptions.find((item) => {
              return item.id === _t.temp.bank;
            });
            let bankName = ''
            bankName = obj.name;
            myData.parentBankId = _t.temp.bank;
            myData.parentBankName = bankName
            // }
            addlstPt(myData)
              .then(res => {
                if (res.success) {
                  _t.$message.success("添加成功");
                  // _t.$refs[temp].resetFields();
                  _t.resetForm();
                  _t.map.clearOverlays();
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.myLoading = false;
              });
          }
        } else {

          return false;
        }
      });
    },
    //提交车辆打卡
    clockSubmit(temp) {
      let t = this;
      t.$refs[temp].validate((valid, obj) => {
        if (valid) {
          t.myLoading = true;
          let newPts = t.temp.pts.split(",");
          newPts.push(newPts[0]);
          newPts = newPts.toString();
          if (t.id) {
            // 编辑
            let myData = {
              id: t.id,
              name: t.temp.roadName,
              address: t.temp.networkSearchVal,
              // contentType: t.temp.networkVal,
              geom: `POLYGON((${newPts}))`,
              // radius: t.myRadius,
              // geomtype: t.prevData.geomtype,

              // status:_t.temp.status

            };
            //  if(t.temp.sendTime){
            // myData.sendTime=t.temp.sendTime;
            // }
            // if(t.temp.receiveTime){
            // myData.receiveTime=t.temp.receiveTime;
            // }
            // let obj = {};
            //   obj = this.bankOptions.find((item) => {
            //     return item.id === _t.temp.bank;
            //   });
            //   let bankName = ''
            //   bankName = obj.name;
            //   myData.parentBankId = _t.temp.bank;
            //   myData.parentBankName = bankName
            editlstPt(myData)
              .then(res => {
                if (res.success) {
                  t.$message.success("编辑成功");
                } else {
                  t.$message.error(res.msg);
                }
              })
              .finally(() => {
                t.myLoading = false;
              });
          } else {
            // 添加
            let myData = {
              name: t.temp.roadName,
              address: t.temp.networkSearchVal,
              pts: newPts

            };
            //  if(t.temp.sendTime){
            // myData.sendTime=t.temp.sendTime;
            // }
            // if(t.temp.receiveTime){
            // myData.receiveTime=t.temp.receiveTime;
            // }
            checkArea(myData)
              .then(res => {
                if (res.success) {
                  t.$message.success("添加成功");
                  t.resetForm();
                  t.map.clearOverlays();
                } else {
                  t.$message.error(res.msg);
                }
              })
              .finally(() => {
                t.myLoading = false;
              });
          }
        } else {
          return false;
        }
      });
    },
    resetForm() {
      // 重置部分表单
      let t = this;
      t.temp.roadName = ""; // 网点名称
      t.temp.networkSearchVal = ""; // 搜索地址
      t.temp.finalMarkePoints = ""; // 经纬度
      t.temp.pts = ""; // 地图区域
    },
    setPlace() {
      // 搜索后设置标注
      var that = this;
      this.map.clearOverlays(); //清除地图上所有覆盖物

      var local = new BMap.LocalSearch(this.map, {
        //智能搜索
        onSearchComplete: this.myFun
      });
      that.networkAddLocal = local;
      local.search(that.temp.networkSearchVal);
    },
    myFun() {
      var _t = this;
      _t.userlocation = _t.networkAddLocal.getResults().getPoi(0).point; //获取第一个智能搜索的结果
      _t.drawMarker(_t.userlocation); // 添加标注
    },
    // 返回
    goBack() {
         this.$router.push({name:'网点档案',params:{page:this.$route.params.page,limit:this.$route.params.limit}});
    },
    // 处理传过来的数据
    getPrevData() {
      let _t = this;
      _t.prevData = this.$route.params; // 传过来的数据
      // console.log('传过来的数据')
      // console.log(_t.prevData)
      if (_t.prevData.id) {
        // 有id为编辑
        _t.id = _t.prevData.id;
        _t.temp.roadName = _t.prevData.name;
        _t.temp.networkSearchVal = _t.prevData.address;
        _t.temp.networkVal = _t.prevData.contentType;
        _t.temp.bank = _t.prevData.parentBankId;
        _t.temp.status = _t.prevData.status;
        _t.temp.needToReport = _t.prevData.needToReport;
        if (_t.prevData.sendTime) {
          _t.temp.sendTime = _t.prevData.sendTime
        }
        if (_t.prevData.receiveTime) {
          _t.temp.receiveTime = _t.prevData.receiveTime
        }

        // geom转成lnglat
        if (_t.prevData.contentType == 7) {
          // 车辆打卡区域
          _t.clockIn = true;
          let geoms = _t.prevData.geom.slice(9, -2);
          _t.drawPolygon(geoms);
        } else {
          // let regex = /POINT\((.*?)\)/ig;
          let geom = _t.prevData.geom.slice(6, -1).split(" ");
          // 编辑初始化
          _t.myRadius = _t.prevData.radius;
          // _t.temp.finalMarkePoints = geom[0]+','+geom[1];
          _t.drawMarker(new BMap.Point(geom[0], geom[1])); // 需要触发drawMarker()
        }
      } else {
        // 无id为添加
        // console.log('add')
      }
    },
    // 地图标注方法
    drawMarker(P) {
      // P为point对象

      let _t = this,
        myMartker = new BMap.Marker(P, { enableDragging: true });
      _t.myCircle = new BMap.Circle(P, _t.myRadius, {
        strokeColor: "blue",
        strokeWeight: 2,
        strokeOpacity: 0.5
      });
      // 赋值经纬度
      _t.temp.finalMarkePoints = `${P.lng},${P.lat}`;
      _t.$refs.temp.validateField("finalMarkePoints"); // 验证经纬度
      // 描点
      _t.map.clearOverlays();
      if (!_t.clockIn) {
        // 不是车辆安全区域时
        _t.map.addOverlay(_t.myCircle); //非打卡区域时添加圆
        // 通过圆的覆盖范围设置地图
        let myBounds = _t.myCircle.getBounds();
        let sw = myBounds.getSouthWest(),
          ne = myBounds.getNorthEast();
        let result = [
          sw,
          ne,
          new BMap.Point(sw.lng, ne.lat),
          new BMap.Point(ne.lng, sw.lat)
        ];
        _t.map.setViewport(result, { margins: [100, 100, 100, 100] });
      } else {
        let myZoom = _t.map.getZoom();
        _t.map.centerAndZoom(P, myZoom > 4 ? myZoom : 17);
      }

      _t.map.addOverlay(myMartker); //添加标注
      _t.myM = myMartker;
      // 标记拖拽事件
      myMartker.addEventListener("dragend", function (e) {
        // console.warn(e);
        _t.temp.finalMarkePoints = `${e.point.lng},${e.point.lat}`;
      });
      myMartker.addEventListener("dragging", function (e) {
        // console.warn(e);
        _t.myCircle.setCenter(e.point);
      });
    },
    // 格式化滑块
    formatTooltip(val) {
      let value = this.myRadius;
      if (value > 1000) {
        return value / 1000 + "公里";
      } else {
        return value + " 米";
      }
    },
    changeRadius() {
      // 修改圆半径
      this.myCircle && this.myCircle.setRadius(this.myRadius);
    },
    setPoint() {
      // 设置网点
      let t = this,
        myFun = function (x) {
          t.drawMarker(x.point); // 添加标注
          t.map.removeEventListener("click", myFun);
        };
      t.map.addEventListener("click", myFun);
    },
    setArea() {
      // 设置区域
      let t = this;
      t.myPolygon && this.map.removeOverlay(t.myPolygon); // 清除多边形
      t.myDrawingManager = new BMapLib.DrawingManager(t.map, t.myDobj); // 添加绘图模式
      t.myDrawingManager.open();
      t.myDrawingManager.setDrawingMode(BMAP_DRAWING_POLYGON);
      t.myDrawingManager.addEventListener("polygoncomplete", function (
        e,
        overlay
      ) {
        t.temp.pts = ""; // 清除数据
        t.myPolygon = overlay;
        overlay.getPath().forEach(x => {
          // 后台需要的格式
          t.temp.pts += `${x.lng} ${x.lat},`;
        });
        t.temp.pts = t.temp.pts.slice(0, -1);
      });
    },
    clearAll() {
      // 清除
      this.map.clearOverlays();
      this.temp.finalMarkePoints = ``;
      this.temp.pts = "";
      this.myM = undefined;
    },
    changeType(e) {
      // 车辆打卡区域需要渲染不一样的操作(type:7)
      let t = this;
      if (e == 7) {
        // 车辆打卡区域
        t.clockIn = true;
        t.myCircle && t.map.removeOverlay(t.myCircle); // 清除圆
      } else {
        t.clockIn = false;
        t.myPolygon && this.map.removeOverlay(t.myPolygon); // 清除多边形
        t.temp.pts = ""; // 清除数据
        t.myM && t.myCircle && t.map.addOverlay(t.myCircle); // 添加圆
      }
    },
    drawPolygon(list) {
      // 画多边形
      let t = this,
        map = t.map,
        paths = list.split(","),
        result = [];
      paths.pop();
      t.temp.pts = paths.toString();
      paths.forEach(x => {
        let res = x.split(" ");
        result.push(new BMap.Point(res[0], res[1]));
      });
      t.map.clearOverlays();
      t.map.setViewport(result, { margins: [100, 100, 100, 100] });
      // 添加多边形
      t.myPolygon = new BMap.Polygon(result, {
        strokeColor: "red", //边线颜色。
        fillColor: "#e28080", //填充颜色。当参数为空时，圆形将没有填充效果。
        strokeWeight: 3, //边线的宽度，以像素为单位。
        strokeOpacity: 0.8, //边线透明度，取值范围0 - 1。
        fillOpacity: 0.6, //填充的透明度，取值范围0 - 1。
        strokeStyle: "solid" //边线的样式，solid或dashed。
      });
      map.addOverlay(t.myPolygon);
    },
    //初始化下拉框
    getSelect() {
      let that = this;

      selectBank().then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            // that.networkOptions = [{ id: -1, name: "请选择" }];
            return;

          }

          that.bankOptions = res.data;

          // that.networkOptions.unshift({ id: -1, name: "请选择" });
        } else {
          if (res.data == null) {
            this.bankOptions = [];
          }
          this.$message.error(res.msg);
        }
      });



    },
  }
};
</script>
