import request from '../../request'
//网点列表
export function lstPt(data) {
    return request({
        url: '/map/lstPt',
        method: 'post',
        data
    })
}
//查询所有网点
export function allDestination(data) { 
  return request({
      url: '/map/allDestination',
      method: 'post',
      data
  })
}
//新增网点
export function addlstPt(data) {
  return request({
    url: '/map/keypt',
    method: 'post',
    data
  })
}
//编辑网点
export function editlstPt(data) {
  return request({
    url: '/map/updatePt',
    method: 'post',
    data,
    headers:{'content-type':'application/json'}
  })
}
//刪除网点
export function dellstPt(data) {
  return request({
    url: '/map/deletePt',
    method: 'post',
    data
  })
}
//新增区域
export function checkArea(data) {
  return request({
    url: '/map/checkArea',
    method: 'post',
    data
  })
}

//查询路线下网点
export function destinationByRoad(data) { 
  return request({
      url: '/map/destinationByRoad',
      method: 'post',
      data
  })
}
